import apiClient from '@/app/api-client'
import { Button } from '@/components/ui/button'

import React, { useEffect, useState } from 'react'

const DoctorApproval = () => {
    const [pending, setPending] = useState([])
    const fetchPendingDoctor = async () => {
        const response = await apiClient.get("/doctorkycs?status=pending")
        setPending(response.data)
    }
    useEffect(() => {
        fetchPendingDoctor()
    }, [])
    return (
        <>{!pending && pending.length === 0}?<p>No Doctor to appoint</p>:
            <ul>{pending.map(item => <li key={item._id}>{item.fullname} <Button asChild> Approve</Button></li>)}</ul>
        </>)
}

export default DoctorApproval